import Vue from 'vue'
import Router from 'vue-router'


import about from './views/about'
import contact from './views/contact'
import card from './components/Card'

Vue.use(Router)

export default new Router({
    mode: 'history',
    routes: [
        {
            path: '/',
            component: card,
            props: true
        },
        {
            path: '/contact',
            component: contact
        },
        {
            path: '/about',
            component: about
        }
    ]
})